import React, { Fragment } from 'react';
import Header from "./Header"
import Footer from "./Footer"
import Banner from "./Home/Banner"
import Solution from "./Home/Solution"
import Institute from "./Institute";

const Home=()=>{
    return(
        <Fragment>
                <Header />
                <Banner />
                <Solution />
                <Institute />
                <Footer />
        </Fragment>
    )
}

export default Home;