import React, { Fragment, useState } from "react";
import HeadingText from "./HeadingText"

const Institute =()=>{
    const [ajsn, useJsn] = useState([
        {
            id: 1,
            block: "images/ins1.jpg"
        },
        {
            id: 2,
            block: "images/ins2.jpg"
        },
        {
            id: 3,
            block: "images/ins3.jpg"
        },
        {
            id: 4,
            block: "images/ins4.jpg"
        },
        {
            id: 5,
            block: "images/ins5.jpg"
        }
    ])

    return(
        <Fragment>
            <section className="instituteDiv">
            <div className="container">
                <HeadingText text="Edu-Cloud Institutes" />
                <ul>
                    {ajsn.map(function(v,i){
                        return <li key={i}><label><img src={v.block} alt="" /></label></li>
                    })}
                    
                    
                </ul>
            </div>
            </section>
        </Fragment>
    )
}

export default Institute;