import React, { Fragment, useState } from "react"

const Tocom =()=>{
    const [inpt, setInpt] = useState("")
    const [item, setItem] = useState([])
   const inputEvent=(e)=>{
    setInpt(e.target.value)
    }

    const toEvent=()=>{
        setItem((old)=>{
            return [...old,inpt]
        })
    }
    return(
        <Fragment>
            <div className="todo">
             <h1>this is todo list</h1>
            <input type="text" value={inpt} onChange={inputEvent} />
            <button onClick={toEvent}>Add</button>
            <ul>
                {item.map((v,i)=>{
                    return <li key={i}>{v}</li>
                })}
            </ul>
            </div>
        </Fragment>
    )
}


export default Tocom;
