import React, { Fragment } from 'react';

const Banner=()=>{
    return(
        <Fragment>
            <section className="bannerDiv">
                <img src={`${process.env.PUBLIC_URL}/images/banner.jpg`} src="images/banner.jpg" alt="" />
                <div className="bannerDivIn">
                    <div className="container">
                    <aside>
                        <h1>Become a ‘Trusted Cloud’  Campus</h1>
                        <p>Empower your institute with best-of-breed cloud services and so</p>
                        <a href="#" className="enroolNow">Enroll now</a>
                    </aside>
                    </div>
                </div>
                </section>
        </Fragment>
    )
}

export default Banner;