import React, { Fragment } from "react"
import {Link, NavLink} from "react-router-dom"

const Navigation =()=>{
    return(
        <Fragment>
             <ul className="navidiv">
                 <li>
                     <Link exact to="./">Home</Link>
                 </li>
                 <li><Link to="./todo">Todo</Link></li>
             </ul>
        </Fragment>
    )
}

export default Navigation;