import React, { Fragment, useState } from 'react';
import CounterClass from "./CounterClass"

const Counter =()=>{
    const [click, setClick] = useState(0);
    const [tri, setTri] = useState(10); 

    const triggerBtn =()=>{
        console.log(tri)
        if(tri < 55){
            setTri(tri +5)
        } else{
            setTri("Thank You")
        }        
    }
    

    return(
        <Fragment>
            <div className="counter">    
                <h1>this is function count</h1>           
               <button onClick={() => setClick(click + 1)}>Click me {click}</button>
               <button onClick={triggerBtn}>Click me {tri}</button>
               <CounterClass />
            </div>
        </Fragment>
    )
}


export default Counter;