import React, { Component, Fragment } from  "react";
import HeadingText from "../HeadingText"

class Solution extends Component{
    constructor(props){
        super(props)
        console.log("constructor")
        this.state= {            
                name: "amar Kumar Poddar",
                age: 30,
                solution: [
                    {
                    img: "/images/img1.jpg",
                    heading: "Communication and Collaboration",
                    content: `<ul><li>50 GB e-mail box with school domain (e.g. <a href="mailto:yourname@yourschooldomain.in">yourname@yourschooldomain.in</a>)</li><li>Online sharing of documents</li></ul>`,
                    },
                    {
                        img: "/images/img2.jpg",
                        heading: "Productivity Tools and Platform",
                        content: `<ul><li>Upgrade to the latest version of Windows 10</li><li>Faculty and students can use Office ProPlus Suite on up to five different devices</li><li>Collaborate and co-create content for classes, assignments and projects</li></ul>`,
                    },
                    {
                        img: "/images/img3.jpg",
                        heading: "Cloud Services and Storage",
                        content: `<ul><li>1.0 TB of cloud storage for your content on OneDrive</li><li>Mobile Device Management with Microsoft Intune</li></ul>`,
                    },
                    {
                        img: "/images/img4.jpg",
                        heading: "Rich Learning Resources",
                        content: `<ul><li>Access to the latest learning content on Microsoft technologies with Microsoft Imagine Academy</li><li>Get students and faculty industry ready with Microsoft Certifications</li></ul>`,
                    }
                ]                
            }

            this.eventHandler = this.eventHandler.bind(this)
            
    }    

    getData(){
        this.setState({
            age: 'Done'
          })
      }
    

      componentDidMount(){        
        setTimeout(() => {
            this.getData();            
          }, 10000)
      }

    rendomNumber=()=>{
        return this.setState({
             age: "Done"
         })
     }


    componentDidUpdate(){
        console.log("componentDidUpdate")
    }
    componentWillUnmount(){
        console.log("componentWillUnmount")
    }

    eventHandler(){
        this.setState({
            age: this.state.age + 1
        }        
        )
    }

    render(){
        console.log("render")
        const {solution} = this.state
        return(
            <Fragment>
                
                <button onClick={this.eventHandler}>Click {this.state.name} {this.state.age} </button>

                <section className="solutionDiv">
                <div className="container">                 
                <HeadingText text="Solution components for your campus" />
               
                    <div className="solutionWrap">
                    { solution.map(function(v,i){
                        return <div key={i} className="loopDiv">
                        <img src={`${process.env.PUBLIC_URL}${v.img}`} alt="" />
                        <h3>{v.heading}</h3>                        
                        <div dangerouslySetInnerHTML={{__html: v.content}} />
                    </div>
                    })}
                
                    </div>
                </div>
            </section>
            </Fragment>
        )
    }
}

export default Solution;


