import React, { Component, Fragment } from "react"

class CounterClass extends Component{
    constructor(){
        super()
        this.state={
            count: 0
        }
    }

   stetEvent=()=>{
        this.setState({
            count: this.state.count+1
        })
    }

    render(){
        return(
            <Fragment>
                <h1>this is state count</h1>
                <button className="setBtn" onClick={this.stetEvent}>State count {this.state.count}</button>
            </Fragment>
        )
    }
}

export default CounterClass;